// @ts-check
// Module rioned/conformance
// Handle the conformance section properly.
// based on W3C conformance, but because rioned has different requirements, have a separate module
import { html } from "../core/import-maps.js";
export const name = "rioned/conformance";

/**
 * @param {Element} conformance
 */
function processConformance(conformance) {
  const content = html`
    <h2>Conformiteit</h2>
    <p>
      Alles in dit document is niet normatief.
    </p>
  `;
  conformance.prepend(...content.childNodes);
}

export function run() {
  const conformance = document.querySelector("section#conformance");
  if (conformance) {
    processConformance(conformance);
  }
}
